.wp-block-cuicpro-cuicpro-teams {
	background-color: #ffffff;
	padding: 20px;
	border-radius: 1px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	min-height: 700px;
	display: flex;
	flex-direction: column;
	gap: 20px;

	.tournaments-list-container {
		display: flex;
		gap: 20px;
	}

	.tournament-item {
		display: flex;
		background: #555555;
		padding: 10px;
		border-radius: 15px;
		border: 1px solid black;
		color: #fff;
		cursor: pointer;
		font-weight: 600;
	}

	.tournament-item:hover {
		background: #90dffc;
	}

	.tournament-item[selected] {
		background: #f58400;
		color: black;
		font-weight: 600;
	}

	.tournament-item-header {
		display: flex;
		align-items: center;
		font-size: 14px;
		text-align: center;
	}

	.tournament-item-name {
		font-size: 14px;
		text-align: center;
	}
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-container p {
	color: black;
	font-size: 12px;
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-title {
	display: flex;
	justify-content: center;
	align-items: center;
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-title h2 {
	color: black;
	font-size: 16px;
	font-weight: bold;
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-container {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
	column-gap: 30px;
	row-gap: 20px;
	padding: 10px 20px;
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-item {
	display: flex;
	flex-direction: row;
	color: white;
	padding: 10px 10px;
	align-items: center;
	gap: 10px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	transition: all 0.3s ease-in-out;
}

.wp-block-cuicpro-cuicpro-teams .teams-grid-item:hover {
	background-color: #a4af7c;
	cursor: pointer;
	transition: all 0.3s ease-in-out;
}
